uto;\n    margin-top: 5px;\n  } \n\n  #modalContainer {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.8);\n    display: none;\n    z-index: 9999;\n  }\n  \n  #modalContainer.show {\n    display: flex;\n  }\n  \n  .video-container {\n    position: relative;\n    max-width: 50%;\n    max-height: 50%;\n    margin: 0 auto;\n  }\n  \n  .video-container video {\n    display: block;\n    width: 100%;\n    height: 100%;\n    object-fit: contain;\n  }\n\n.right {\n  left: 450px;\n}\n\n.curisidades {\n  margin-top: 100px;\n  background-color: #202123;\n  padding: 60px;\n  border-radius: 6px;\n}\n\n  .curisidades p, h1 {\n    color: #ececf1;\n    text-align: justify;\n  }\n\n  .curisidades h1 {\n    font-size: 26px;\n  }\n\n  .curisidades img {\n    height: 100%;\n    width: auto;\n    padding: 20px;\n  }\n\n  .curisidades p {\n    font-size: 16px;\n  }\n\n.curisidade {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  flex-direction: column;\n  margin-bottom: 100px;\n}\n\n.part2 {\n  flex-direction: row-reverse;\n}\n\n.made {\n  margin-top: 50px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  color: #ececf1;\n}\n\n.img-container {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  align-items: center;\n  margin-top: 50px;\n}\n\n.gpt {\n  width: 50px !important;\n  height: auto;\n}\n\n.logos {\n  width: 100px;\n  margin-left: 30px;\n  height: auto;\n}\n\n.scroll-to-top {\n  position: fixed;\n  bottom: 20px;\n  right: 20px;\n  background-color: rgba(128, 128, 128, 0.5);\n  border: 0;\n  border-radius: 6px;\n  cursor: pointer;\n}\n\n  .scroll-to-top:hover {\n    background-color: rgba(128, 128, 128, 0.5);\n    border: 0;\n  }\n\n  .title-timeline {\n    text-align: center;\n    justify-content: center;\n    align-items: center;\n    padding: 15px;\n    color: #ececf1;\n    fo